<template>
  <div class="py-2 bg-white rounded-lg">
    <div class="flex justify-center m-3">
      <div class="w-96">
        <div class="flex m-1 justify-center items-center">
          <div class="inline-flex p-1 text-right w-24">아이디:</div>
          <input
            class="inline-flex p-1 border-gray-600 border-2 rounded-md"
            v-model="id"
          />
        </div>

        <div class="flex m-1 justify-center items-center mt-4 bg-gray-300">
          <div class="inline-flex p-1 text-right w-24">암호</div>
          <input
            class="inline-flex p-1 border-gray-600 border-2 rounded-md"
            type="password"
            v-model="pw"
          />
        </div>

        <div class="flex mt-3 justify-center items-center">
          <div class="inline-flex text-center text-red-600 font-bold">
            {{ errorMessage }}
          </div>
        </div>

        <div class="flex justify-center mt-3">
          <button type="button" class="tw-indigo-btn" @click="onLogin()">
            로그인
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      pw: "",
      errorMessage: "",
      waitResponse: false,
    };
  },
  mounted() {
    if (this.$store.getters.isLogin) {
      this.$router.push("/profile");
      return;
    }
  },
  methods: {
    async onLogin() {
      if (this.id < 4) {
        this.errorMessage = "아이디를 확인해주세요";
        return;
      }
      if (this.pw < 1) {
        this.errorMessage = "암호를 입력해주세요";
        return;
      }

      const loginToken = this.$store.getGUID();

      const reqData = {
        api: "login",
        id: this.id,
        pw: this.pw,
        loginToken: loginToken,
      };

      this.waitResponse = true;
      const res = await this.$store.callAsync(reqData);
      this.waitResponse = false;

      if (res.code != 100) {
        console.log("login ng", res.message);
        this.errorMessage = res.message;
        return;
      }

      console.log("login ok", res);
      this.$store.commit("setUser", res.user);
      localStorage.setItem("loginToken", res.user.loginToken);
      this.$router.push("/");
    },
  },
};
</script>
